import React from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Url from 'url';

import { Layout, PostCard, HeaderIndexAlt, Tag } from '../components/common';
import { StickyNavContainer } from '../components/common/effects';
import { WebsiteMeta } from '../components/common/meta';
import ArrowRightIcon from '../components/common/icons/arrow-right';

/**
 * Main index page (home page)
 *
 * Loads all posts from Ghost
 *
 */
const Index = ({ location }) => {
  const {
    site: {
      siteMetadata: { description, siteUrl },
    },
  } = useStaticQuery(graphql`
    query SiteMetaDataQueryIndex {
      site {
        siteMetadata {
          description
          siteUrl
        }
      }
    }
  `);

  const canonical = Url.resolve(siteUrl, location.pathname);

  const publishedAt = new Date('2020-04-06T22:00:00');

  const posts = [
    {
      id: 1,
      slug: 'kcdc-covid-19-response-guidelines',
      title: 'KCDC COVID-19 Response Guidelines translation v1.0 released',
      excerpt:
        "Today, we're releasing the English version 1.0 of Korea CDCâ€™s COVID-19 playbook for local governments, complete with appendices. This is the amazing work of global volunteers working around the clock since March 28, 2020. This playbook contains detailed, updated protocols for healthcare providers, government officials and ordinary citizens regarding quarantine, treatment, disinfection, funeral arrangements, and more from both a policy and a patient care perspective. We hope making this document available to a broader audience will save lives and help fight this pandemic.",
      primary_author: {
        name: 'COVID Translate Team',
        slug: 'SLUG',
      },
      authors: [{ name: 'COVID Translate Team', slug: 'SLUG' }],
      primary_tag: { name: 'COVID-19 Response Guidelines' },
      published_at: publishedAt.toISOString(),
      published_at_pretty: publishedAt.toDateString(),
      // feature_image: fluid.src,
      // featureImageSharp: file,
    },
  ];

  const publishedAt2 = new Date('2020-04-09T22:30:00');
  const morePosts = [
    {
      id: 2,
      slug: 'covid-19-test-site-installation-guide',
      title: 'COVID-19 test site installation guide',
      excerpt:
        'While we were focused on getting v1.0 out, a graduate student from France reached out to us about a test site installation guide that his group of volunteers worked on. Thank you for sharing! Click here to download the guides.',
      primary_author: {
        name: 'COVID Translate Team',
        slug: 'SLUG',
      },
      authors: [{ name: 'COVID Translate Team', slug: 'SLUG' }],
      primary_tag: { name: 'COVID-19 Resource' },
      published_at: publishedAt2.toISOString(),
      published_at_pretty: publishedAt2.toDateString(),
    },
  ];

  return (
    <>
      <WebsiteMeta
        type="WebSite"
        canonical={canonical}
        title="Home"
        description={description}
      />
      <StickyNavContainer
        activeClass="fixed-nav-active"
        render={sticky => (
          <Layout isHome header={<HeaderIndexAlt />} sticky={sticky}>
            <div className="inner posts">
              <div className="home-cta">
                <Link to="/kcdc-covid-19-response-guidelines/">
                  <b>Get the playbook</b>
                </Link>
                &nbsp;&nbsp;
                <ArrowRightIcon />
              </div>
              <div className="post-feed">
                {posts.map((p, i) => (
                  <PostCard
                    key={p.id}
                    post={p}
                    num={i}
                    isHome
                    label={<Tag label={'Updated'} />}
                  />
                ))}
              </div>
              <div className="post-feed">
                {morePosts.map((p, i) => (
                  <PostCard
                    key={p.id}
                    post={p}
                    num={i}
                    isHome
                    label={<Tag label={'New'} isNew />}
                  />
                ))}
              </div>
            </div>
          </Layout>
        )}
      />
    </>
  );
};

Index.propTypes = {
  location: PropTypes.object.isRequired,
};

export default Index;
